*   ::after, ::befor {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

html {
    font-size: 10px; }

body {
    font-family: 'Roboto';
    font-weight: 400;
    color: #777777;
    font-size: 1.6rem;
    letter-spacing: .2rem;
    padding: 5rem;
    background: white; }

.conteiner {
    background: #F7F7F7; }

.header {
    position: relative;
    height: 95vh;
    background: linear-gradient(90deg, rgba(186, 133, 84, 0.7) 0.03%, rgba(1, 1, 1, 0.7) 99.94%), url(../img/header-background.jpg) center / cover no-repeat;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%);
    &__logo {
        position: absolute;
        top: 5rem;
        left: 5rem;
        height: 7rem;
        width: 5.6rem; }
    &__box {
        position: absolute;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        text-align: center; } }
.title-first {
    color: #FFFFFF;
    &__main {
        display: block;
        text-transform: uppercase;
        font-weight: 500;
        font-size: 5rem;
        line-height: 6rem;
        letter-spacing: 1rem;
        margin-bottom: 1rem;
        animation-name: leftToRihgt;
        animation-duration: 1.7s;
        animation-delay: 1s;
        animation-timing-function: ease;
        backface-visibility: hidden; }
    &__secondary {
        display: block;
        font-weight: 100;
        font-size: 4rem;
        line-height: 4.8rem;
        letter-spacing: .5rem;
        animation-name: RihgtToLeft;
        animation-duration: 1.7s;
        animation-delay: 1s;
        animation-timing-function: ease;
        backface-visibility: hidden;
        margin-bottom: 6.5rem; } }
.btn {
    &:link, &:visited {
        text-transform: uppercase;
        text-decoration: none;
        font-weight: 400;
        font-size: 2rem;
        line-height: 2.4rem;
        letter-spacing: .5rem;
        color: #777777;
        background: white;
        padding: 1.5rem 4rem;
        border-radius: 5rem;
        display: inline-block;
        transition: all .3s;
        position: relative; }

    &:hover {
        transform: translateY(-4px);
        box-shadow: (0px 4px 4px rgba(0, 0, 0, 0.3));
        &::after {
            opacity: 0;
            transform: scaleX(1.5) scaleY(1.7); } }

    &:active {
        transform: translateY(-2px);
        box-shadow: (0px 4px 4px rgba(0, 0, 0, 0.1)); }
    &::after {
        content: "";
        background: white;
        width: 100%;
        height: 100%;
        border-radius: 5rem;
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        transition: all .4s; }
    &_animated {
        animation: BtnToUp 1s 1s ease-out;
        animation-fill-mode: backwards; } }

@keyframes BtnToUp {
    0% {
        opacity: 0;
        transform: translateY(5rem); }

    100% {
        opacity: 1;
        transform: translateY(0px); } }

@keyframes leftToRihgt {
    0% {
        opacity: 0;
        transform: translate(-10rem); }
    80% {
        transform: translate(1rem); }
    100% {
        opacity: 1;
        transform: translate(0px); } }

@keyframes RihgtToLeft {
    0% {
        opacity: 0;
        transform: translate(10rem); }
    80% {
        transform: translate(-1rem); }
    100% {
        opacity: 1;
        transform: translate(0px); } }













